import './Login.scss';
import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { Input, Button } from 'antd';
import { EyeInvisibleOutlined, EyeTwoTone } from '@ant-design/icons';
import bg from '../../assets/images/login_bg.png';
import icon from '../../assets/images/default_icon.png';
class Login extends Component {
    constructor(props) {
        super(props)
        this.state = {
            name: '',
            password: ''
        }
        this.handleNameChange = this.handleNameChange.bind(this)
        this.handlePassChange = this.handlePassChange.bind(this)
        this.handleClick = this.handleClick.bind(this)
    }
    handleNameChange(e) {
        this.setState({
            name: e.target.value
        })
    }
    handlePassChange(e) {
        this.setState({
            password: e.target.value
        })
    }
    handleClick() {
        console.log(this.state)
        this.props.history.push('/index/message');
    }
    render() {
        return (
            <div className="login-wrap">
                <div className="left">
                    <img className="login-bg" src={bg} alt="" />
                </div>
                <div className="right">
                    <img className="icon" src={icon} alt="" />
                    <div className="btn-block">
                        <Input
                        allowClear
                        placeholder="input name"
                        value={this.state.name}
                        onChange={this.handleNameChange}
                        />
                        <Input.Password
                        allowClear
                        placeholder="input password"
                        value={this.state.password}
                        onChange={this.handlePassChange}
                        iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                        />
                        <Button type="primary" shape="round" block={true} onClick={this.handleClick}>登录</Button>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(Login);